@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Oswald&display=swap);
.navbar {
    position: fixed;
    background: black;
    display: grid;
    grid-template-columns: auto 1fr;
}

/* .menu{
    display: grid;
} */

.grup{
    display: grid;
   grid-column-gap: 3rem;
    grid-template-columns: repeat(3,1fr);
}

.list{
    color: whitesmoke;
    text-decoration: none;
    align-content: center;
    list-style-type: none;
    font-size: 1.5rem;
    text-transform: uppercase;
    letter-spacing: 0.1rem;
}

.active{
    color: green;
}
body{
    margin: 0px;
    padding: 0px;
    background: rgb(104, 86, 86);
    font-family: 'Josefin Sans', sans-serif;
}
.jumbotron h2{
    color: green;
    letter-spacing: 2px;
    font-size: 3rem;
    text-align: center;
}

.jumbotron{
    margin: 5rem;
    min-width: 768px;
    padding: 5rem;
}

.mainform{
    margin-top: 3rem;
    text-align: center;
}

.mainform input{
    border-radius: 0.2rem;
    background: green;
    border: none;
    height: 2rem;
    margin-right: 1rem;
}
.mainform input::-webkit-input-placeholder {
    color: white;
    letter-spacing: 1px;
    text-transform: capitalize;
    margin: auto;
}
.mainform input:-ms-input-placeholder {
    color: white;
    letter-spacing: 1px;
    text-transform: capitalize;
    margin: auto;
}
.mainform input::-ms-input-placeholder {
    color: white;
    letter-spacing: 1px;
    text-transform: capitalize;
    margin: auto;
}
.mainform input::placeholder {
    color: white;
    letter-spacing: 1px;
    text-transform: capitalize;
    margin: auto;
}
.mainform input ::text{
    
    font-weight: bolder;
}
.input{
    color: wheat;
    text-transform: capitalize;
    width: 60%;
    text-align: center;
}
.input:hover{
    border: 4px solid black;
}

.mainform .submit{
    background: rgb(5, 5, 5);
    color: white;
}
.mainform .submit:hover{
    background: green;
    border-radius: 3px;
}
.city{
    color: darkgreen;
    background: black;
    font-weight: bolder;
    font-size: 3rem;
    text-align: center;
}
.color{
    margin-top: 2rem;
    background: rgb(243, 239, 239);
}

.citydata{
box-shadow: 11px 13px 12px 0px rgba(0,0,0,0.75);
    background: cornsilk;
display: flexbox;
margin-top: 3rem;
border: 1px solid black;
text-align: center;
margin-left: 20rem;
width: 50%;
}

.citydata p{
    font-weight: bold;
    color: green;
    font-size: 1rem;
    letter-spacing: 1px;
    text-transform: uppercase;
    text-decoration: underline;
}

.citydata img {
    margin-top: 1rem;
    margin-bottom: 1rem;
    width: 100%;
    height: 20%;
}

.citydata h3 {
    color: green;
    font-weight: bolder;
    font-size: 2rem;
}

.citydata h4{
    color: grey;
    font-size: 1rem;
}
